.containerAboutMe {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 850px;
    background-color: #141414;
}

@media screen and (max-width: 1440px) {
    .containerAboutMe {
        height: 950px;
    }
}

@media screen and (max-width: 1024px) {
    .containerAboutMe {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 1700px;
    }
}

@media screen and (max-width: 542px) {
    .containerAboutMe {
        height: 2390px;
    }

}

.aboutMe {
    margin-left: 2rem;
    /* margin-top: 2rem; */
    color: #fff;
    font-family: 'Noto Sans JP', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;  
    height: 800px;  
    gap: 2rem;
}

.aboutMe>h3 {
    line-height: 35px;
    font-weight: 400;
    width: 900px;
}

.aboutMe>h1{
    font-weight: 400;
    font-size: 50px;
}

@media screen and (max-width: 1440px) {
    .aboutMe {
        width: 800px;
        height: 900px;
    }

    .aboutMe>h3 {
        width: 700px;
    }

}

@media screen and (max-width: 768px) {
    .aboutMe {
        width: auto;
    }
}

@media screen and (max-width: 542px) {
    .aboutMe {
        width: 400px;
        margin-left: 0;
        height: 1300px;
    }

    .aboutMe>h3 {
        width: 380px;
    }
}

.expertise {
    color: #fff;
    font-family: 'Noto Sans JP', sans-serif;
    border-top: 1px solid white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 400px;
    gap: 2rem;
}

.expertise>h3 {
    line-height: 35px;
    font-weight: 400;
    width: 900px;
}

.expertise>h1 {
    font-weight: 400;
    font-size: 50px;
}

@media screen and (max-width: 1440px) {
    .expertise>h3 {
        width: 700px;
    }
}

@media screen and (max-width: 542px) {
    .expertise>h3 {
        width: 380px;
    }

    .expertise>h1 {
        width: 300px;
    }

    .expertise {
        height: 800px;
    }
}

.skills {
    color: #fff;
    font-family: 'Noto Sans JP', sans-serif;
    border-left: 1px solid white;
    border-bottom: 1px solid white;
    height: 800px;
}

.skills>h1 {
    margin-left: 4rem;
    margin-top: 2rem;
    font-weight: 400;
    font-size: 50px;
}

.skills>.sectionOptions {
    display: grid;
    grid-template-columns: 4fr  ;
    grid-column-gap: 10px;
    grid-row-gap: 2rem;
    align-items: center;
    margin-top: 3rem;
    justify-content: center;
}

.skills>.sectionOptions>.option {
    border: 1px solid white;
    width: 100px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 2rem;
    transition: background-color 0.5s ease;
    margin-top: 7.5rem;
}



@media screen and (max-width: 1440px) {
    .skills {
        height: 850px;
    }

    .skills>.sectionOptions {
        margin-left: 5rem;
    }

    .skills>.sectionOptions>.option {
        margin-top: 10rem;
    }
}

@media screen and (max-width: 1024px) {
    .skills {
        border: 1px solid white;
        height: 600px;
        margin-top: 1rem;
    }

    .skills>h1 {
        margin-left: 3.5rem;
    }

    .skills>.sectionOptions {
        width: 600px;
        grid-template-columns: 4fr ;
        grid-column-gap: 10px;
        grid-row-gap: 2rem;
        align-items: center;
        margin-left: -0.5rem;
    }

    .skills>.sectionOptions>.option {
        margin-top: 5rem;
    }
}

@media screen and (max-width: 542px) {
    .skills {
        height: 1050px
    }

    .skills>h1 {
        margin-left: 1rem;
    }

    .skills>.sectionOptions {
        width: 400px;
        grid-template-columns: 2fr;
        margin-left: 0rem;
        grid-row-gap: 0rem;
    }

    .skills>.sectionOptions>.option {
        margin-left: 3rem;
    }
}


