.containerNav {
    display: flex;
    width: 100%;
    height: 70px;
    background-color: #141414;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 999;
}

@keyframes rotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

.containerNav>span {
    color: #fff;
    font-size: 35px;
    position: absolute;
    left: 1.5rem;
    animation: rotation 3s linear infinite;
}

.list {
    display: flex;
    align-items: center;
    color: #fff
}

.list>a {
    text-decoration: none;
    color: #fff
}

.listOptions {
    display: flex;
    margin: 0 1rem 0 1rem;
    font-family: 'Noto Sans JP', sans-serif;
}