.hero{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #141414;
    flex-direction: column;
    position: relative;
}

.containerTitle {
    width: 890px;
    height: 202px;
    position: absolute;
    right: 24.5rem;
    top: 10rem;
    text-align: left;
}

@media screen and (max-width: 1440px) {
    .containerTitle {
        right: 12rem;
        top: 98px;
    }
}

@media screen and (max-width: 1024px) {
    .containerTitle {
        right: -0rem;
        top: 98px;
        width: 784px;
    }
}

@media screen and (max-width: 768px) {
    .containerTitle {
        left: 11rem;
        width: 584px;
    }
}


@media screen and (max-width: 542px) {
    .containerTitle {
        left: 1rem;
        width: 384px;
    }
}