.containerLoading {
    background-color: black;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

@keyframes rotation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

.loading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}  

.loading>span {
    color: rgb(255, 255, 255);
    font-size: 90px;
    animation: rotation 3s linear infinite;
}

