.containerProjects {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 900px;
    background-color: #fff;
}

@media screen and (max-width: 768px) {
    .containerProjects {
        height: 700px;
    }
}

@media screen and (max-width: 542px) {
    .containerProjects {
        justify-content: center;
        align-items: flex-start;
        height: 600px;
    }
}

.containerProjects>h1 {
    margin-left: 2rem;
    color: black;
    font-family: 'Noto Sans JP', sans-serif;
    font-size: 50px;
    margin-bottom: 2rem;
    
}

@media screen and (max-width: 542px) {
    .containerProjects>h1 {
       font-size: 40px;
       margin-bottom: 2rem;
    }
}

.carrousel{
    height: 600px;
}

@media screen and (max-width: 542px) {
    .carrousel {
        height: 200px;
    }
}


.cardProject {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    width: 800px;
    height: 600px;
}

.cardProject>img {
    margin-top: -3rem;
}

@media screen and (max-width: 1440px) {
    .cardProject {
        width: 600px;
    }

    .cardProject>img {
        width: 500px;
        height: 300px;
    }

}

@media screen and (max-width: 1024px) {
    .cardProject {
        width: 450px;
    }

    .cardProject>img {
        width: 400px;
        height: 200px;
    }
}

@media screen and (max-width: 768px) {
    .cardProject {
        width: 600px;
    }

    .cardProject>img {
        width: 500px;
        height: 300px;
    }
}

@media screen and (max-width: 542px) {
    .cardProject {
        width: 500px;
        margin-left: -4rem;
        ;
    }

    .cardProject>img {
        width: 300px;
        height: 200px;
    }

    .carrousel {
        margin: 0;
        width: 100%;
    }
}


.subtitle {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 1rem;
    color: black;
    font-family: 'Noto Sans JP', sans-serif;
    font-size: 20px;
    width: 400px;
    height: 90px;
    border: 1px solid black;
    opacity: 1;
}

.subtitle>p {
    margin: 0.5rem;
}

@media screen and (max-width: 542px) {
    .subtitle {
        width: 300px;
    }
}

.react-3d-carousel .slider-container .slider-left div{
    color: black;
    border: none;
}

.react-3d-carousel .slider-container .slider-right div{
    color: black;
    border: none;
}