.containerFooter {
    display: flex;
    background-color: black;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;;
}

.containerFooter>p {
    color: white;
    margin-right: 2rem;
    font-family: 'Noto Sans JP', sans-serif;
}

.sectionContact {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 300px;
    margin-left: 2rem;
}

.sectionContact>a {
    color: white;
    font-size: 25px;
}

.sectionContact>span {
    color: white;
    font-size: 25px;
}


@media screen and (max-width: 542px) {
    .sectionContact {
        margin-left: 1;
        width: 100px;
    }
}